import React,{useEffect} from 'react';
import { MessagesProps } from '../../Types';
import { useTranslation } from 'react-i18next';
import {Layout,Row,Col,Image, Typography, Space} from 'antd';
import { useHistory } from 'react-router-dom';
import './messages.scss';
export const Messages:React.FC<MessagesProps> = ({background,type,options,urlBtn}) => {
    const history = useHistory();
    const {t,i18n} = useTranslation();
    useEffect(()=>{},[t]);
    const {Content} = Layout;
    const {Title,Paragraph} = Typography;
    const goTo = (e:any) => {
        if(typeof e !== 'string' || e === undefined){
            history.goBack();
        }else{
            history.push(e);
        }
    }
    return (
        <Layout className={background}>
            <Content>
                <Row justify='center' style={{height:'100vh'}}>
                    <Col xxl={24} style={{textAlign:'center',alignItems:'center',display:'flex',justifyContent:'center'}}>
                        <Space direction='vertical' style={{width:'360pt'}}>
                            {options.visible === true ?
                        <Image src={options.src} preview={{visible:false, mask:false}} /> : null
                            }
                        <Title >{t(`messages.${type}.title`)}</Title>
                        <Paragraph >{t(`messages.${type}.paragraph`)}</Paragraph>
                        <button onClick={()=>goTo(urlBtn)} className={`${type}-btn`}>{t(`messages.${type}.btn`)}</button>
                        </Space>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}