import React,{useEffect} from 'react';
import { Row, Col, Layout, Divider,Typography,Space,Button, Image, Checkbox,Table } from 'antd';
import { useTranslation } from 'react-i18next';
import './privacy.scss';
import { ResizeGlobalContext } from '../../context/screen';
export const Privacy:React.FC = () => {
    const {resize} = ResizeGlobalContext();
    const {Content} = Layout;
    const {t,i18n} = useTranslation();
    const {Title,Paragraph,Text} = Typography;
    useEffect(()=>{},[t]);
    const items:any = t("privacy.content.row-two.list-items",{returnObjects:true});
    const items_row:any = t("privacy.content.row-three.list-titles",{returnObjects:true});
    const items_five:any = t("privacy.content.row-five.items",{returnObjects:true});
    const table:any = t("privacy.table.titles",{returnObjects:true});
    const table_columns = table.map((e:any,index:any)=>{
        return {
            title:e.title,
            dataIndex:e.title.toLowerCase(),
            key:index
        }
    })
    return (
        <Layout className='bg-white'>
            <Content>
                <Row justify='center'>
                    <Col xxl={16} lg={16} xs={16} sm={16}>
                        <Title className='dark-blue privacy-title'>{t("privacy.title")}</Title>
                        <div className='offset-bottom-2'>
                        <Paragraph className='dark-blue privacy-paragraph'>{t("privacy.content.row-one.title")}</Paragraph>
                        <Text className='dark-blue privacy-content'>{t("privacy.content.row-one.paragraph")}</Text>
                        </div>
                        <div className='offset-bottom-2'>
                        <Paragraph className='dark-blue privacy-paragraph'>{t("privacy.content.row-two.title")}</Paragraph>
                        <ul className='privacy-list dark-blue'>
                            {t("privacy.content.row-two.list-title")}
                            {items?.map((e:any,index:any)=>(
                                <li key={index} className='privacy-item'>
                                    {e}
                                </li>
                            ))}
                        </ul>
                        </div>
                        <div className='offset-bottom-2'>
                            <Paragraph className='dark-blue privacy-paragraph'>{t("privacy.content.row-three.title")}</Paragraph>
                            <ul className='privacy-list dark-blue'>
                                {items_row.map((e:any,index:any)=>(
                                    <li key={index} className='privacy-item'>{e.title}
                                    <br/>
                                        <Text>{e.paragraph}</Text>
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <div className='offset-bottom-2'>
                                <Paragraph className='dark-blue privacy-paragraph'>{t('privacy.content.row-four.title')}</Paragraph>
                                <Text className='dark-blue privacy-content'>{t("privacy.content.row-four.paragraph")}</Text>
                        </div>
                        <div className='offset-bottom-4'>
                            <Paragraph className='dark-blue privacy-paragraph'>
                                    {t("privacy.content.row-five.title")}
                            </Paragraph>
                            <ul className='privacy-list dark-blue'>
                                    {items_five.map((e:any,index:any)=>(
                                        <li key={index}>
                                            {e}
                                        </li>
                                    ))}
                            </ul>
                        </div>
                        <div className='offset-bottom-2'>
                                    <Paragraph className={`dark-blue privacy-paragraph  ${resize === false ?'padding-right-25':null}`}>
                                        {t("privacy.table.title")}
                                    </Paragraph>
                                   <Table columns={table_columns} scroll={{x:100}} />
                        </div>
                    </Col>
                </Row>
            </Content>
        </Layout>
    )
}